.InputPost{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}  

 .textInputLabel{
    display: grid;
    grid-area:  InputPost;
   
    height: 64px;
    padding: 1rem 1rem;

    resize: none;
    
    color: #212529;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

    font-weight: 400;
    font-size: 23px;
    line-height: 100%;

    margin: 20px;
    margin-right: 0px;
    padding-left: 30px;
    padding-right: 30px;

    width: 100%; 
     
}

*, *::before, *::after {
    box-sizing: border-box;
}

.catPaw{  
    height: 100%;
}
.buttonCatPaw{
    grid-area: Button;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5em;
    height: 5em;
    margin-left: 5%; 
       
} 
